import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { AllocateComponent } from './allocate/allocate.component';
import { FeedbackComponent } from './feedback/feedback.component';
import { PageNotFoundComponent } from './page-not-found/page-not-found.component';
import { FeedbackFormComponent } from './feedback-form/feedback-form.component';
import { ApplicationRecordsComponent } from './application-records/application-records.component';
import { ExperienceComponent } from './experience/experience.component';
import { ContactDetailsComponent } from './contact-details/contact-details.component';


const routes: Routes = [
  { path: '', redirectTo: '/allocate', pathMatch: 'full'},
  { path: 'allocate', component: AllocateComponent},
  { path: 'feedback', component: FeedbackComponent},
  { path: 'feedback/empId', component: ApplicationRecordsComponent},
  { 
    path: 'feedback/:empId/:id', 
    component: FeedbackFormComponent, 
    children: [
      { path: 'experience', component:  ExperienceComponent},
      { path: 'contact-details', component: ContactDetailsComponent }
    ]
  },
  { path: "**", component: PageNotFoundComponent}
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { } 
export const routingComponents = [AllocateComponent, 
  FeedbackComponent,
  ApplicationRecordsComponent,
  FeedbackFormComponent,
  ExperienceComponent,
  ContactDetailsComponent,
  PageNotFoundComponent]
