import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router, ParamMap } from '@angular/router';
import { FeedbackData } from './feedback-data';

@Component({
  selector: 'app-feedback-form',
  templateUrl: './feedback-form.component.html',
  styles: []
})
export class FeedbackFormComponent implements OnInit {

  
  feedbackModel = new FeedbackData('Rob', 'rob@test.com', 3247168215, 'java', 'pass', 'nothing much but, looks like a descent kid');
  public programmingLanguages = ['C', 'C++', 'Java', 'HTML', 'Python', 'CSS'];
  public applicationRecordId;
  constructor(private route: ActivatedRoute, private router: Router) { }

  ngOnInit() {
    //let id = parseInt(this.route.snapshot.paramMap.get('id'));
    //this.applicationRecordId = id;
    this.route.paramMap.subscribe((params: ParamMap) => {
      let id = parseInt(params.get('id'));
      this.applicationRecordId = id;
    })
  }

  goPrevious(){
    let previousId = this.applicationRecordId - 1;
    this.router.navigate(['/feedback/empId', previousId]);
  }

  goNext(){
    let nextId = this.applicationRecordId + 1;
    this.router.navigate(['/feedback/empId', nextId]);
  }

  goToApplicationRecords(){
    let selectedId = this.applicationRecordId ? this.applicationRecordId : null;
    //this.router.navigate(['/feedback/empId', { id: selectedId }])
    this.router.navigate(['../', { id:selectedId }], { relativeTo: this.route });
  }

  showExperience(){
    this.router.navigate(['experience'], { relativeTo: this.route });
  }

  showContactDetails(){
    this.router.navigate(['contact-details'], { relativeTo: this.route });
  }

}
